import uniPopup from '@/components/uni-popup/uni-popup'
import merchantActive from '@/components/active-item'
import merchantComment from '@/components/comment-item'
import { mapState, mapActions } from 'vuex'
import { commentActive, giveLike, transpondActive, replyComment } from '@/api'
export default {
	name: '',
	data () {
		return {
			id: '',
			comment: '',  // 评论内容
			text: '',   // 转发内容
			isFocus: false,
			showPopup: false,
			commentType: 1,  // 默认1 评论动态   2回复评论
			hid: '',   // 回复的评论id
		}
	},
	onLoad (options) {
		this.id = options.id
		this._reqGetActiveDetail()
	},
	onShow () {
		this.$hideTabbarButton()
	},
	computed: {
		...mapState(['active_detail']),
	},
	methods: {
		...mapActions(['reqGetActiveDetail']),
		async onReplyComment (id) {  // 回复评论
			this.isFocus = true;
			this.hid = id;
			this.commentType = 2;
		},
		async onConfirmCommentClick () {   // 评论
			let self = this;
			if(this.comment == '') {
				uni.showToast({
					title: '请输入评论内容',
					icon: 'none'
				})
				return;
			}
			if(this.commentType == 1) {
				let result = await commentActive ({
					token: uni.getStorageSync('token'),
					mid: this.id,
					text: this.comment
				})
				this.comment = ''
				if(result.code == 200) {
					self._reqGetActiveDetail()
					let view = uni.createSelectorQuery().select(".wrapper");
					view.boundingClientRect(data => {
						console.log("得到布局位置信息" + JSON.stringify(data));
						uni.pageScrollTo({
							scrollTop: data.bottom,
							duration: 300
						});
					}).exec();
					
				}
			}
			if(this.commentType == 2) {    // 回复评论
				let result = await replyComment ({
					token: uni.getStorageSync('token'),
					text: this.comment,
					cid: this.hid,
					mid: this.id
				})
				this.commentType = 1;
				this.comment = '';
				if(result.code == 2000) {
					self._reqGetActiveDetail()
				}
			}
		},
		async onActiveButtonClick ({type, id}) {
			if(type == 1) {     // 评论
				this.isFocus = true;
			}
			if(type == 2) {     // 点赞
				let result = await giveLike ({
					token: uni.getStorageSync('token'),
					mid: id
				})
				if(result.code == 200) {
					this._reqGetActiveDetail()
				}
			}
			if(type == 3) {     // 转发
				this.showPopup = true
			}
		},
		async onConfirmClick () {   // 确定转发
			let self = this;
			let result = await transpondActive ({
				token: uni.getStorageSync('token'),
				text: this.text,
				zid: this.id
			})
			if(result.code == 200) {
				uni.showToast({
					title: '转发成功'
				})
				setTimeout(() => {
					this.showPopup = false,
					this.text = '';
					self._reqGetActiveDetail()
				}, 1500)
			}
		},
		onBlurEvent () {
			this.isFocus = false;
		},
		_reqGetActiveDetail () {
			this.reqGetActiveDetail({
				token: uni.getStorageSync('token'),
				moving_id: this.id
			})
		},
		togglePopup () {
			this.showPopup = false;
		}
	},
	components: {
		merchantActive,
		merchantComment,
		uniPopup
	}
}
